import React, { useState, useEffect } from 'react';
import { HashRouter as Router } from 'react-router-dom';
import SpotifyAuth from './SpotifyAuth';
import Calendar from './Calendar';

function App() {
  const [token, setToken] = useState('');

  useEffect(() => {
    // Check for token in localStorage on component mount
    const storedToken = localStorage.getItem('spotify_token');
    if (storedToken) {
      setToken(storedToken);
    }

    // Check for token in URL (after Spotify redirect)
    const hash = window.location.hash
      .substring(1)
      .split('&')
      .reduce((initial, item) => {
        let parts = item.split('=');
        initial[parts[0]] = decodeURIComponent(parts[1]);
        return initial;
      }, {});

    if (hash.access_token) {
      setToken(hash.access_token);
      localStorage.setItem('spotify_token', hash.access_token);
      // Clear the hash from the URL
      window.location.hash = '';
    }
  }, []);

  const handleAuthenticated = (newToken) => {
    setToken(newToken);
    localStorage.setItem('spotify_token', newToken);
  };

  const handleLogout = () => {
    setToken('');
    localStorage.removeItem('spotify_token');
  };

  return (
    <Router>
      <div className="App">
        <h1>Spotify Calendar</h1>
        {!token ? (
          <SpotifyAuth onAuthenticated={handleAuthenticated} />
        ) : (
          <>
            <button onClick={handleLogout}>Logout</button>
            <Calendar token={token} />
          </>
        )}
      </div>
    </Router>
  );
}

export default App;
