// src/Calendar.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import moment from 'moment';
import { Container, Row, Col, Card, Form, Spinner, Alert, Button } from 'react-bootstrap';
import { SketchPicker } from 'react-color';

const Calendar = ({ token }) => {
  const [playlists, setPlaylists] = useState([]);
  const [selectedPlaylist, setSelectedPlaylist] = useState(null);
  const [tracks, setTracks] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [frameColor, setFrameColor] = useState('#ffffff');

  useEffect(() => {
    const fetchPlaylists = async () => {
      setLoading(true);
      try {
        const response = await axios.get('https://api.spotify.com/v1/me/playlists', {
          headers: { 'Authorization': `Bearer ${token}` }
        });
        setPlaylists(response.data.items);
        setLoading(false);
      } catch (error) {
        console.error('Error fetching playlists:', error);
        setError('Failed to fetch playlists');
        setLoading(false);
      }
    };

    if (token) {
      fetchPlaylists();
    }
  }, [token]);

  const handlePlaylistSelect = async (event) => {
    const playlistId = event.target.value;
    setSelectedPlaylist(playlistId);
    if (playlistId) {
      setLoading(true);
      try {
        const response = await axios.get(`https://api.spotify.com/v1/playlists/${playlistId}/tracks`, {
          headers: { 'Authorization': `Bearer ${token}` }
        });
        setTracks(response.data.items);
        setLoading(false);
      } catch (error) {
        console.error('Error fetching tracks:', error);
        setError('Failed to fetch tracks');
        setLoading(false);
      }
    } else {
      setTracks([]);
    }
  };

  const handleColorChange = (color) => {
    setFrameColor(color.hex);
  };

  const renderTracksByDate = () => {
    const tracksByDate = tracks.reduce((acc, track) => {
      const date = moment(track.added_at).format('YYYY-MM-DD');
      if (!acc[date]) {
        acc[date] = [];
      }
      acc[date].push(track);
      return acc;
    }, {});

    return Object.keys(tracksByDate).map(date => (
      <Card key={date} className="mb-3" style={{ borderColor: frameColor }}>
        <Card.Header style={{ backgroundColor: frameColor }}>{moment(date).format('MMMM Do, YYYY')}</Card.Header>
        <Card.Body>
          <ul>
            {tracksByDate[date].map((track, index) => (
              <li key={index} style={{ display: 'flex', alignItems: 'center' }}>
                <img src={track.track.album.images[2].url} alt={track.track.artists[0].name} style={{ width: '40px', height: '40px', marginRight: '10px' }} />
                {track.track.name} - {track.track.artists[0].name}
              </li>
            ))}
          </ul>
        </Card.Body>
      </Card>
    ));
  };

  const renderPlaylist = () => (
    <Card className="mb-3">
      <Card.Header>Playlist</Card.Header>
      <Card.Body>
        <ul>
          {tracks.map((track, index) => (
            <li key={index}>
              {track.track.name} - {track.track.artists[0].name}
            </li>
          ))}
        </ul>
      </Card.Body>
    </Card>
  );

  return (
    <Container>
      <h1 className="my-4 text-center">Your Playlists</h1>
      {error && <Alert variant="danger">{error}</Alert>}
      <Form.Group>
        <Form.Label>Select a playlist:</Form.Label>
        <Form.Control as="select" onChange={handlePlaylistSelect} disabled={loading}>
          <option value="">Choose a playlist</option>
          {playlists.map(playlist => (
            <option key={playlist.id} value={playlist.id}>{playlist.name}</option>
          ))}
        </Form.Control>
      </Form.Group>
      <Button className="my-3" onClick={() => setFrameColor('#ffffff')}>Reset Frame Color</Button>
      <SketchPicker color={frameColor} onChangeComplete={handleColorChange} />
      {loading && <Spinner animation="border" className="my-4" />}
      <Row>
        <Col md={8}>
          {selectedPlaylist && !loading && renderTracksByDate()}
        </Col>
        <Col md={4}>
          {selectedPlaylist && !loading && renderPlaylist()}
        </Col>
      </Row>
    </Container>
  );
};

export default Calendar;