import React, { useEffect } from 'react';

const SpotifyAuth = ({ onAuthenticated }) => {
  useEffect(() => {
    const hash = window.location.hash
      .substring(1)
      .split('&')
      .reduce((initial, item) => {
        let parts = item.split('=');
        initial[parts[0]] = decodeURIComponent(parts[1]);
        return initial;
      }, {});

    // Log the entire hash response for debugging
    console.log('Full hash response:', hash);

    if (hash.access_token) {
      onAuthenticated(hash.access_token);
      // Store the token in localStorage
      localStorage.setItem('spotify_token', hash.access_token);
      // Clear the hash from the URL
      window.location.hash = '';
    } else if (hash.error) {
      console.error('Authentication error:', hash.error);
    }

    // Check for existing token in localStorage
    const storedToken = localStorage.getItem('spotify_token');
    if (storedToken) {
      onAuthenticated(storedToken);
    }
  }, [onAuthenticated]);

  const handleLogin = () => {
    const clientId = '124fc22c3bd0468b85e83a98d0ad2478'; // Your actual Client ID
    const redirectUri = process.env.NODE_ENV === 'production'
      ? 'https://akaalli.github.io/spotify-calendar'
      : 'http://localhost:3000';
    const scopes = encodeURIComponent('playlist-read-private');
    const authEndpoint = 'https://accounts.spotify.com/authorize';

    const loginUrl = `${authEndpoint}?client_id=${clientId}&redirect_uri=${redirectUri}&scope=${scopes}&response_type=token&show_dialog=true`;
    console.log('Login URL:', loginUrl); // For debugging
    window.location = loginUrl;
  };

  return (
    <button onClick={handleLogin}>Login to Spotify</button>
  );
};

export default SpotifyAuth;
